html,
body{
    padding: 0;
    margin: 0;
}
.page{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.calculator{
    border: 1px solid black;
    height: 500px;
    width: 450px;
}
#display{
    height: 40px;
    direction: rtl;
    font-size: 30px;
    padding-right: 10px;
}
.button-grid{
    justify-content: center;
    width: 100%;
    display:grid;
    grid-template-columns: repeat(4, auto);
    grid-template-rows: repeat(6,15%);
    gap: 5px;
}
#equals{
    grid-row: 5 / 7;
    grid-column: 4 / 5;
}
#clear{
    grid-row: 2 / span 1;
    grid-column: 2 / span 1;   
}
#backspace{
    grid-row: 2 / span 1;
    grid-column: 1 / span 1; 
}
#subtract{
    grid-row: 2 / span 1;
    grid-column: 3 / span 1; 
}
#add{
    grid-row: 2 / span 1;
    grid-column: 4 / span 1; 
}
#multiply{
    grid-row: 3 / span 1;
    grid-column: 4 / span 1; 
}
#divide{
    grid-row: 4 / span 1;
    grid-column: 4 / span 1; 
}
#zero{
    grid-row: 6 / span 1;
    grid-column: 1 / span 2; 
}
